  .carousel {
    overflow: hidden;
    /* height: 580px; */
    width: 100%;
    min-width: 1440px;
  }


  @media only screen and (min-width: 480px) and (max-width: 768px) {
    .carousel {
        width: 768px;
    }
  }

  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    .carousel {
        width: 1024px;
    }
  }

  @media only screen and (min-width: 1024px) and (max-width: 1440px) {
    .carousel {
        width: 1024px;
    }
  }

  @media only screen and (min-width: 1441px) {
    .carousel {
        width: 1441px;

    }
  }


  
  .inner {
    white-space: nowrap;
    transition: transform 0.01s;
  }

  
  .carousel-item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    filter: brightness(70%);

    /* position: relative;
    text-align: center; */
  }

  @media only screen and (min-width: 480px) and (max-width: 768px) {
    .carousel-item {
        width: 768px;
    }
  }

  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    .carousel-item {
        width: 1024px;
    }
  }

  @media only screen and (min-width: 1024px) and (max-width: 1440px) {
    .carousel-item {
        width: 1024px;
    }
  }

  @media only screen and (min-width: 1441px) {
    .carousel-item {
        width: 1441px;

    }
  }


  .indicators {
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin: 0% 0.1%; */
  }
  
  .indicators > button {
    height: 15px;
    width: 15px;
    /* margin: 0 2px; */
    background-color: rgb(235, 235, 235);
    border-color: transparent;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
    margin: 0.5%;
    
  }
  
  .indicators > button.active {
    height: 15px;
    width: 15px;
    /* margin: 0 2px; */
    background-color: #bbb;
    border-color: transparent;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
    margin: 0.5%;
  }

