.laoding-and-error {
    font-size: 1em;
    font-family: Arial;
    color: #3f3f3f;
}

.hero {
    width: 50%;
    position: relative;
    justify-content: center;
    text-align: flex-start;
    color: white;
    /* z-index: -1; */
}

@font-face {
    font-family: 'caslon';
    src: url('./fonts/ACaslonPro-Regular.otf')
  }
  
  @font-face {
    font-family: 'springville';
    src: url('./fonts/Springville-Regular3.otf')
  }

.Hero-text {
    position: absolute;
    bottom: 25%;
    padding: 0% 16%;
    /* z-index: 2; */
}

@media only screen and (max-width: 1024px) {
    .Hero-text {
     bottom: 12%;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .Hero-text {
     bottom: 20%;
    }
}

#title {
    font-family: springville;
    font-size: 3em;
}

#date {
    font-family: caslon;
    font-size: 2em;
}

#subtitle {
    font-family: caslon;
    font-size: 1em;
}


